@import url("https://fonts.googleapis.com/css2?family=Festive&display=swap");
div.childpage {
  position: relative;
  width: 100%;
  height: 1200px;
  z-index: 1;
  background-color: #e2dede;
  overflow: hidden;
}

div.article-title {
  margin: 20vw auto 0 auto;
  width: 80vw;
  height: auto;
  font-size: 40px;
  font-style: oblique;
}

div.words {
  margin: 5vw auto;
  width: 80vw;
  height: auto;
  font-size: 24px;
  font-family: "Times New Roman", Times, serif;
}

div.aboutmepage {
  position: relative;
  width: 100%;
  height: 1200px;
  z-index: 1;
  background-color: #e2dede;
  overflow: hidden;
}

div.experiencepage {
  position: relative;
  width: 100%;
  height: 1200px;
  z-index: 1;
  background-color: #e2dede;
  overflow: hidden;
}

div.home-container {
  position: absolute;
  z-index: 1;
  top: 40px;
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

html, body {
  background-color: white;
  color: black;
  font-family: "Lato", sans-serif;
  list-style-type: none;
  overflow-x: hidden;
  margin: 0;
  padding: 0;
}

body {
  overflow: hidden;
}

html {
  overflow-y: scroll;
}

div.HeaderBar {
  margin: 0;
  padding: 0;
  width: 100vw;
  z-index: 2;
  height: 40px;
  display: flex;
  flex-direction: row;
  position: fixed;
  overflow: hidden;
  background-color: white;
}

div.nav-logo {
  width: 5%;
  height: 100%;
  float: left;
  margin-left: 20px;
  margin-top: 10px;
}
div.nav-logo:hover {
  cursor: pointer;
}

div.logo {
  text-align: center;
  width: 90%;
  font-family: "Festive", cursive;
  font-size: x-large;
}

div.master-container {
  position: fixed;
  top: 40px;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background-size: cover;
  background-repeat: no-repeat;
  background-blend-mode: saturation;
}

div.summary {
  max-width: 400px;
  max-height: 500px;
  margin-top: 100px;
  margin-left: 50px;
  font-size: 60px;
  display: flex;
  flex-direction: column;
  color: #271a70;
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
}

div.ABOUTME:hover {
  color: yellowgreen;
  cursor: pointer;
}

div.EXPERIENCE:hover {
  color: yellowgreen;
  cursor: pointer;
}

div.SKILL:hover {
  color: yellowgreen;
  cursor: pointer;
}

div.CONTACTME:hover {
  color: yellowgreen;
  cursor: pointer;
}

div.cover {
  z-index: 1;
  position: relative;
  top: 0;
  margin-bottom: 100vh;
  padding: 0;
  width: 100vw;
  height: 100vh;
  background-color: white;
}

div.title {
  font-size: 80px;
  padding-top: 40vh;
  text-align: center;
}

.nav {
  width: 150px;
  height: auto;
  z-index: 3;
  position: fixed;
  top: 40px;
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 18px;
  border: 1px solid grey;
  border-radius: 15px;
  background-color: white;
  padding: 10px 0;
}

div.li {
  list-style-type: none;
  width: 150px;
  height: 30px;
  text-decoration: none;
}
div.li:hover {
  cursor: pointer;
  background-color: grey;
}